@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
html {
  font-size: 10px;
}

body {
  margin: 0;
  font-family: "Roboto Light",
    sans-serif;
}
 


.wrapper {
  background: url(/my-app/static/media/background.ab0c2625.jpg) no-repeat center center fixed;
  background-size: cover;
  position: relative;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.container {
  display: flex;
  position: relative;
  flex-flow: column;
  align-items: center;
}

.padding {
  padding: 3rem;
}

.App {
  width: 626px;
  height: 434px;
  background-color: rgba(43, 49, 45, 0.4);
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  border-radius: 3px;
  color: #999999;
  z-index: 99;
}

.blur {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.2);
  overflow: hidden;
  width: 626px;
  height: 434px;
  -webkit-filter: blur(10px);
  filter: blur(10px);
}


.App-logo {
  height: 40vmin;
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.registration {
  color: rgba(255, 255, 255, 0.8);
  text-decoration: none;
  text-transform: uppercase;
  font-size: 1rem;
  letter-spacing: 0.1rem;
  justify-content: center;
  display: flex;
}

.login-form {
  display: flex;
  position: relative;
  flex-flow: column;
  justify-content: space-around;
  margin-top: 1.5rem;
  height: 265px;
  width: 370px;
}

.login-form__input {
  width: 367px;
  height: 46px;
  border-radius: 25px;
  border: 1px solid rgba(255, 255, 255, 0.6);
  text-align: center;
  font-size: 1.8rem;
  color: rgba(255, 255, 255, 0.788);
}

.login-form__input:not(:focus) {
  background: none;
  font-size: 3rem;
  color: rgba(255, 255, 255, 0.8);
}

.login-form__input:placeholder-shown {
  font-size: 1.8rem;
}

.login-form__input:focus::-webkit-input-placeholder {
  color:transparent;
}

.login-form__input:focus:-ms-input-placeholder {
  color:transparent;
}

.login-form__input:focus::-ms-input-placeholder {
  color:transparent;
}

.login-form__input:focus::placeholder {
  color:transparent;
}

.login-form__input:focus {
  font-size: 3rem;
  background: #426355c5;

}

.login-form__input-valid {
  background: none;
  color: rgba(255, 255, 255, 0.8);
}

.login-form__input-error {
  position: absolute;
  top: 8px;
  right: 12px;
  width: 30px;

}

.login-form__input-check {
  position: absolute;
  top: 18px;
  right: 12px;
}

.login-form__submit {
  color: white;
  align-self: center;
  background: #3b2f38;
  width: 151px;
  height: 48px;
  border-radius: 25px;
  border: 1px solid white;
  font-size: 1.2rem;
  letter-spacing: 0.3rem;
}

.login-form__submit:disabled {
  color: rgba(255, 255, 255, 0.35);
  border: 1px solid rgba(255, 255, 255, 0.35);
  background: none;
}

.login-form__submit:active,
.login-form__submit:focus {
  color: black;
  background-color: white;
}

.login-form_input-wrap {
  position: relative;
}

.navbar {
background-color: rgba(255, 255, 255, 0.7);
}

.clock-wrap {
    text-align:center;
    padding:1em 0;
}

.clock-wrap p {
    text-decoration:none;
    color: white;
}

.clock-wrap span {
    color:white;
}

.nav-item {
    font-size: 15px;
}

#time-block {
    background: url(/my-app/static/media/weather_background.2183e5ee.jpg) no-repeat no-repeat center;
    background-size: cover;
}

.time-block-wrap{
    padding: 5% 0;
}

.user_name {
    font-size: 19px;
    color: rgb(53, 65, 59);
}

.header-text
{
    color: #666
}

.news-wrapper {
    margin-top: 2%;
}

#news {
    box-shadow: 18px  rgba(20, 85, 20, 0.884), 0, 0, 0;
    padding-top: 2%;
}

.exit_btn {
    font-size: 15px;
}
.card-inner-wrap {
    height: 300px;
}

.card-img {
    width: 100%;
}
